@font-face {
  font-family: "Tahoma";
  src: url("../fonts/tahoma.woff2") format("woff2");
}
@font-face {
  font-family: "Avenir-regular";
  src: url("../fonts/Avenir-Roman.woff2") format("woff2");
}
@font-face {
  font-family: "Avenir-bold";
  src: url("../fonts/Avenir-Black.woff2"), format("woff2");
}
@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Oblique.woff2") format("woff2");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

h1 {
  margin: 0;
}

h2 {
  margin: 0;
}

.header {
  height: 110px;
  padding-top: 23px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  background-color: #f3f7ef;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__list {
  display: flex;
  list-style: none;
}

@media (max-width: 768px) {
  .nav__list--mobile {
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }
}

.nav__item {
  margin-right: 50px;
}
@media (max-width: 768px) {
  .nav__item {
    margin-bottom: 20px;
  }
}

.nav__item:last-child {
  margin-left: 23px;
}
@media (max-width: 768px) {
  .nav__item:last-child {
    margin-left: 0;
  }
}

.nav__link {
  color: #2f2a37;
  font-family: Tahoma, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}

.button-lang {
  background-color: transparent;
  border: none;
  outline: none;
  position: relative;
  margin-right: 20px;
  cursor: pointer;
}
.button-lang::after {
  content: "";
  position: absolute;
  right: -20px;
  top: 5px;
  background: url("../../img/svg/Vector.svg");
  height: 7px;
  width: 12px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.nav__item--center {
  margin-left: 28px;
}
@media (max-width: 768px) {
  .nav__item--center {
    margin-left: 0;
  }
}

@media (max-width: 768px) {
  .nav--mobile {
    position: fixed;
    right: 300px;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #f3f7ef;
    z-index: 10;
    border-right: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
    transform: translateX(-100%);
    transition: 0.3s;
  }
}
@media (max-width: 610px) {
  .nav--mobile {
    right: 300px;
    left: -100px;
  }
}
@media (max-width: 510px) {
  .nav--mobile {
    right: 300px;
    left: -200px;
  }
}
@media (max-width: 410px) {
  .nav--mobile {
    right: 210px;
    left: -100px;
  }
}

.nav--active {
  transform: translateX(100%);
}

.close {
  display: none;
}
@media (max-width: 768px) {
  .close {
    display: block;
    margin-top: 40px;
    margin-left: 40px;
    background-color: transparent;
    outline: none;
    border: none;
  }
}

.menu {
  display: none;
}
@media (max-width: 768px) {
  .menu {
    display: block;
    background-color: transparent;
    outline: none;
    border: none;
  }
}

